<template>
  <div>
    <div class="block d-none" id="top"></div>
    <div class="pt-5 d-mb-none"></div>
    <div class="container pt-5">
      <h5 class="mb-5">Для установки более точной цены, нам нужно знать какой функционал вам нужен. Просим оставить нам заявку и мы вскоре свяжеммся с вами.</h5>

      <div class="block">
        <div class="d-flex justify-content-around flex-wrap mb-5">
          <card :card-data="card1"></card>
          <card :card-data="card2"></card>
          <card :card-data="card4"></card>
          <card :card-data="card3"></card>
          <card :card-data="card5"></card>
          <card :card-data="card6"></card>
        </div>
      </div>
    </div>
    <main-form></main-form>
  </div>
</template>

<script>
  import MainForm from '../components/MainForm'
  import card from '../components/Card'
  export default {
    components: {
      card,
      MainForm
    },
    created() {
      window.scrollTo(0, 0);
    },
    data() {
      return {
        card1: {
          header: 'Визитка',
          list: ['Индивидульный дизайн', 'Счетчик просмотров'],
          link: '#form',
          price: 'от 5.000 тг'
        },
        card2: {
          header: 'Лендинг',
          list: ['Индивидульный дизайн', 'Админ панель'],
          link: '#form',
          price: 'от 10.000 тг'
        },
        card3: {
          header: 'Магазин',
          list: ['Индивидульный дизайн', 'Админ панель', 'Техническая поддержка'],
          link: '#form',
          price: 'от 30.000 тг'
        },
        card4: {
          header: 'Блог',
          list: ['Индивидульный дизайн', 'Админ панель', 'Комментарии'],
          link: '#form',
          price: 'от 20.000 тг'
        },
        card5: {
        header: 'Задание',
            list: ['Доработать сайт', 'Написать дизайн', 'Написать скрипт'],
            link: '#form',
            price: 'Уточнить цену'
        },
        card6: {
          header: 'Свой проект',
          list: ['Индивидульный проект'],
          link: '#form',
          price: 'Уточнить цену'
        }
      }
    }
  }
</script>


